.sign_box {
    border-radius: 8px;
    box-shadow: 0 4px 12px rgb(0 0 0 / 15%);
    padding: 24px;
    width: 304px;
    height: 370px;
}

h1 {
    font-weight: 500;
    margin: 20px 0;
}

.forms {
    outline: none;
    margin-top: 50px;
    height: 260px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

}

input {
    height: 16px;
    width: 276px;
    padding: 17px 12px;
    border-radius: 4px;
    border-width: 1px;
    vertical-align: middle;
    font-size: 19px;
    color: rgba(0,0,0,0.9);
    transition: all ease 0.3s;
}

input:focus {
    outline: none !important;
    box-shadow: 0 0 0 1px #0a66c2;
}

button {
    width: 100%;
    height: 52px;
    padding: 0 24px;
    border-radius: 28px;
    border-width: 0;
    background-color: rgb(80, 80, 80);
    color: white;
    transition: all ease 0.3s;
    font-size: 16px;
    font-weight: 500;
    font-family: inherit;
}

button:hover {
    background-color: black;
    cursor: pointer;
}